(function() {
  'use strict';

  angular
    .module('EVQueue', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ui.bootstrap','ui.router']);
})();

(function () {
    'use strict';

    angular
        .module('EVQueue')
        .factory('QueueService', QueueService);


    function QueueService($http, $log, $q,API) {

        return {
            getQueue: getQueue,
            joinQueue:joinQueue
        };

        function getQueue() {
            return $http.get(API)
                .then(getQueueSuccess)
                ['catch'](getQueueFailed);

            function getQueueSuccess(response) {
                return response.data;
            }

            function getQueueFailed(error) {
                $log.error('XHR Failed for getQueue.' + error.data);
                return $q.reject(error);
            }
        }

        function joinQueue() {
            return $http.post(API)
                .then(joinQueueSuccess)
                ['catch'](joinQueueFailed);

            function joinQueueSuccess(response) {
                return response.data;
            }

            function joinQueueFailed(error) {
                $log.error('XHR Failed for getQueue.' + error.data);
                return $q.reject(error);
            }
        }
    }

})();

(function() {
    'use strict';

    angular
        .module('EVQueue')
        .controller('QueueController', MainController);

    /** @ngInject */
    function MainController($log,QueueService,$timeout,LoginService) {
        var vm = this;
        $log.log("QueueController Init");
        vm.user = LoginService.getUser();

        vm.getQueue = function(){
            vm.queue = QueueService.getQueue();
        };

        vm.userIsQueued = false;

        vm.joinQueue = function(){
            vm.userIsQueued = true;
            QueueService.joinQueue(vm.user);
            vm.getQueue();
        };

        $timeout(function(){
            vm.getQueue();
        },30000);

        vm.getQueue();
    }
})();

(function () {
    'use strict';

    angular
        .module('EVQueue')
        .service('LoginService', LoginService);


    function LoginService() {
        var user = null;
        this.logUser = function(username){
            user = username;
        };
        this.getUser = function(){
            return user;
        }
    }

})();

(function() {
    'use strict';

    angular
        .module('EVQueue')
        .controller('LoginController', LoginController);

    /** @ngInject */
    function LoginController($log,LoginService,$state) {
        var vm = this;
        vm.logUser = function(){
            $log.log("user");
            $state.go("queue");
        };

    }
})();

(function() {
  'use strict';

  angular
    .module('EVQueue')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('EVQueue')
    .constant('AppName', 'EVQueue')
    .constant('API', '/api/');

})();

(function () {
    'use strict';

    angular
        .module('EVQueue')
        .config(config);

    /** @ngInject */
    function config($logProvider, $stateProvider,$urlRouterProvider) {
        // Enable log
        $logProvider.debugEnabled(true);

        $urlRouterProvider.otherwise('/login');

        $stateProvider
            .state('login', {
                url: '/login',
                templateUrl: 'app/login/login.html',
                controller: 'LoginController',
                controllerAs: 'vm'
            })
            .state('queue', {
                url: '/queue',
                templateUrl: 'app/queue/queue.html',
                controller: 'QueueController',
                controllerAs: 'vm'
            });
    }

})();

angular.module("myNewProject").run(["$templateCache", function($templateCache) {$templateCache.put("app/login/login.html","<div class=\"row marketing\"><form><div class=\"form-group\"><label for=\"username\">Username</label> <input type=\"text\" id=\"username\" placeholder=\"username\"></div><button type=\"button\" class=\"btn btn-primary\" ng-click=\"vm.logUser()\">Login</button></form></div>");
$templateCache.put("app/queue/queue.html","<div class=\"container\"><button type=\"button\" class=\"btn btn-info\" ng-click=\"vm.joinQueue()\">Refresh List</button> <button type=\"button\" class=\"btn btn-info\" ng-disabled=\"vm.userIsQueued\" ng-click=\"vm.joinQueue()\">Join Queue</button><div ng-repeat=\"q in vm.queue\"><div ng-bind=\"q.username\"></div></div></div>");}]);